.excursion {
  padding: 0;
  margin: 0;
  list-style: none;  
  border: 0.3px solid #828282;
  border-radius: 16px;
  box-sizing: border-box;
  overflow: hidden;
  display: flex;
  position: relative;
}

@media screen and (max-width: 1024px) {
  .excursion {
    flex-direction: column;
  }
}